//预约练车
<template>
	<view>
		<view class="userMsg">
			<view class="header">
				<view class="name">
					<view class="text">姓名</view>
					<input type="text" placeholder="请输入姓名" v-model="name">
				</view>
				<view class="phone">
					<view class="text">手机号码</view>
					<input type="number" placeholder="请输入手机号" v-model="phone" maxlength="11">
				</view>
				<view class="type son">
					<view class="text">驾照类型</view>
					<picker mode="selector" :range="typeName" @change="changeType" class="typeChoose">
						<view>{{type}}</view>
					</picker>
					<image :src="cdn+'/icon/triangle.svg'" class="triangle" mode=""></image>
				</view>
				<view class="course son">
					<view class="text">练车科目</view>
					<picker mode="selector" :range="courseType" @change="chooseCourse" class="courseChoose">
						<view>{{course}}</view>
					</picker>
					<image :src="cdn+'/icon/triangle.svg'" class="triangle" mode=""></image>
				</view>
			</view>

			<view class="body">
				<view class="isShuttle">
					<view class="title">是否接送</view>
					<switch :checked="isShuttle" color="#29c563" style="transform:scale(0.8)" @change="shuttle" />
				</view>

				<view class="practiceTime son">
					<view class="text">
						练车时间
					</view>
					<picker mode="selector" :range="timeList" :range-key="'carTime'" @change="chooseTime" :value="pTime"
						class="chooseTime ">
						<view>{{pTime}}</view>
					</picker>
					<image :src="cdn+'/icon/triangle.svg'" class="triangle" mode=""></image>
				</view>

				<!-- <view class="shuttleTime son" v-if="isShuttle">
					<view class="text">接送时间</view>
					<picker mode="selector" :range="shuttleTime" @change="chooseShuttleTime" class="shuttle">
						<view>{{sTime}}</view>
					</picker>
					<image :src="cdn+'/icon/triangle.svg'" class="triangle" mode=""></image>
				</view> -->
				<view class="sAddress son" v-if="isShuttle">
					<view class="text">接送地址</view>
					<input type="text" :disabled="true" v-model="address" placeholder="请选择地址" @click="chooseAddress()">
					<image :src="cdn+'/icon/triangle.svg'" class="triangle" mode=""></image>
				</view>
				<button :disabled="isSub" @click="toBooking()">确定</button>
			</view>
		</view>

	</view>
</template>

<script>
	import {
		mapActions,
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				cdn: this.$conf.CDN_HOST,
				isShuttle: false,
				isSub:false,
				type: '请选择驾照类型',
				typeName: ['c1(手动档)', 'c2(自动挡)', 'c3(小车)', 'A2(货车)', 'B2(货车)', 'A1(客车)', 'A3(客车)', 'B1(客车)', 'D(摩托车)',
					'E(摩托车)', 'F(摩托车)'
				],
				pTime: '请选择练车时间',
				sTime: '请选择接送时间',
				course: '请选择练车科目',
				practiceTime: ['7月28日14:00到17:00', '7月29日14:00到17:00', '7月30日14:00到17:00', '7月31日14:00到17:00'],
				shuttleTime: ['7月28日14:00', '7月29日14:00'],
				courseType: ['科目二', '科目三'],
				name: null,
				phone: null,
				jiesongStatu: null,
				carTime: null,
				mpTime: null,
				address: null,
				addressLongitude: null,
				addressLatitude: null,
			}
		},
		methods: {
			...mapActions({
				toBooking_fun: 'booking/toPractice',
				getPTime_fun: 'booking/getPTime',
			}),
			//选择驾照类型
			changeType(e) {
				this.type = this.typeName[e.detail.value]
			},
			//选择练车时间
			chooseTime(e) {
				this.pTime = this.timeList[e.detail.value].carTime
			},
			//选择练车科目
			chooseCourse(e) {
				this.course = this.courseType[e.detail.value]
			},
			//选择接送时间
			chooseShuttleTime(e) {
				this.sTime = this.shuttleTime[e.detail.value]
			},
			shuttle() {
				this.isShuttle = !this.isShuttle
			},
			async toBooking() {
				let jiesongStatu = 0
				if (this.isShuttle) {
					jiesongStatu = 1
				}
				if(this.name&&this.phone&&this.type!='请选择驾照类型'&&this.course!='请选择练车科目'&&this.pTime!='请选择练车时间')
				{
					if(this.phone.length<11){
						this.$win.nlog('手机号不足11位')
						return
					}
					this.isSub=true
					let data = {
						userId: this.$db.getSync('userId'),
						name: this.name,
						phone: this.phone,
						carType: this.type,
						object: this.course,
						jiesongStatu,
						carTime: this.pTime,
						mptime: this.sTime,
						address: this.address,
						longitude: this.addressLongitude,
						latitude: this.addressLatitude
					}
					let res = await this.toBooking_fun(data)
				}
				else {
					this.$win.nlog('请填写完整信息')
					return
				}
			},
			chooseAddress() {
				uni.getLocation({
					type: 'gcj02',
					success: (res) => {
						const latitude = res.latitude;
						const longitude = res.longitude;
						uni.chooseLocation({
							latitude: latitude,
							longitude: longitude,
							success: (res1) => {
								// this.address=res1.address+' '+res1.address
								this.address = res1.address
								this.addressLatitude = res1.latitude
								this.addressLongitude = res1.longitude
							}
						});
					}
				})
			},
			async getTime() {
				let data = {

				}
				let res = await this.getPTime_fun(data)
			}
		},
		computed: {
			...mapState({
				pRes: state => state.booking.pRes,
				timeList:state=>state.booking.timeList
			})
		},
		onShow() {
			this.getTime()
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f2f3f5;
	}

	/* 上半部分start */
	.userMsg .header {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 750rpx;
		padding: 0 0 50rpx;
		background: #ffffff;
	}

	.text {
		box-sizing: border-box;
		padding-left: 30rpx;
		width: 172rpx;
		height: 90rpx;
		font-size: 28rpx;
		font-family: Source Han Sans CN, Source Han Sans CN-Regular;
		font-weight: 400;
		text-align: left;
		color: #666666;
		line-height: 90rpx;
		/* 	background-color: skyblue;
	border-radius: 8rpx; */
	}

	.name,
	.phone,
	.type,
	.course {
		display: flex;
		width: 690rpx;
		height: 90rpx;
		margin-top: 30rpx;
		background: #f2f2f2;
		border-radius: 8rpx;
	}

	.name input,
	.phone input,
	.sAddress input {
		/* 		background-color: pink; */
		height: 100%;
		width: 578rpx;
		border-radius: 8rpx;
		font-size: 28rpx;
		font-family: Source Han Sans CN, Source Han Sans CN-Regular;
		font-weight: 400;
		text-align: left;
		color: #999999;
	}

	.typeChoose,
	.courseChoose {
		width: 578rpx;
		height: 100%;
		font-size: 28rpx;
		font-family: Source Han Sans CN, Source Han Sans CN-Regular;
		font-weight: 400;
		text-align: left;
		color: #999999;
		line-height: 90rpx;
		/* background-color: pink; */
	}

	/* 上半部分end */


	/* 下半部分start */

	.body {
		position: relative;
		display: flex;
		flex-direction: column;
		align-items: center;
		margin-top: 20rpx;
		padding: 40rpx 0;
		height: 750rpx;
		background-color: #ffffff;
	}


	.isShuttle {
		box-sizing: border-box;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		width: 100%;
		padding: 0 30rpx 10rpx;
	}

	.isShuttle .title {
		width: 128rpx;
		height: 100%;
		font-size: 32rpx;
		font-family: Source Han Sans CN, Source Han Sans CN-Regular;
		font-weight: 400;
		text-align: left;
		color: #333333;
		line-height: 60rpx;
	}

	.practiceDate,
	.practiceTime,
	.shuttleTime,
	.sAddress {
		display: flex;
		width: 690rpx;
		height: 90rpx;
		margin-top: 30rpx;
		background: #f2f2f2;
		border-radius: 8rpx;
	}



	.chooseTime,
	.shuttle {
		width: 578rpx;
		height: 100%;
		font-size: 28rpx;
		font-family: Source Han Sans CN, Source Han Sans CN-Regular;
		font-weight: 400;
		text-align: left;
		color: #999999;
		line-height: 90rpx;
	}

	button {
		position: absolute;
		top: 520rpx;
		font-size: 32rpx;
		color: #fff;
		width: 580rpx;
		height: 90rpx;
		line-height: 90rpx;
		background: linear-gradient(134deg, #54d685, #29c563 99%);
		border-radius: 45rpx;
	}

	/* 下半部分end */

	.son {
		position: relative;

		.triangle {
			position: absolute;
			top: 40rpx;
			right: 40rpx;
			width: 25rpx;
			height: 18rpx;
		}
	}
</style>
